@charset "utf-8";
.top{
    /* background-image: linear-gradient(to bottom,#CCC,#111); */
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 58px;
    z-index: 9999;
    background-color: #fff;    
    font-family: 'Roboto Condensed', sans-serif;
}
.gnb{
    max-width: 1320px;
    margin: 0 auto;
}
.gnb ul{
    display: flex;
    align-items: center;
    list-style: none;
    gap: 0 20px
}
.gnb>ul>li{
    /* 상위메뉴 부모자격 */
    position: relative;
    line-height: 58px;
}
.gnb a{
    color: #111;
    font-weight: bold;
    text-decoration: none;
    transition: .3s;
}
.gnb a:hover{    
    color: #007aff;
}

.smenu {
    position: absolute;
    left: 50%;
    transform: translateX(-50%) translateY(10px);
    background-color: #fff;
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0px 0px 5px rgba(0,0,0,0.3);
    white-space: nowrap;
    line-height: 2.5;
    display: none;
}

.gnb>ul>li:hover .smenu{
    display: block;
}

.smenu::before {
    content: '';
    position: absolute;
    top: -15px;
    left: 50%;
    transform: translateX(-50%);
    width: 0px;
    height: 0;
    border-bottom: 15px solid rgba(0,0,0,0.2);
    border-left: 15px solid transparent;
    border-right: 15px solid transparent;
}
.smenu::after {
    content: '';
    position: absolute;
    top: -17px;
    left: 50%;
    transform: translateX(-50%);
    width: 0px;
    height: 0;
    border-bottom: 15px solid #fff;
    border-left: 15px solid transparent;
    border-right: 15px solid transparent;
}

h1{
    text-align: center;
}

/* 메인영역 */


/* 하단영역 */
.info{
    text-align: center;
    padding: 30px;
    background-color: #000;
    color: #fff;
    display: flex;
    flex-direction: column;
}
.info>ul>li:last-child{
    font-weight: bold;
}
.bmenu{
    font-family: 'Roboto Condensed', sans-serif;
    display: flex;
    justify-content: center;
    margin: 15px 0;
}
.bmenu li{
    padding: 0 10px;
}
.bmenu li + li{
    border-left: 2px solid #fff;
}
.bmenu a{
    color: #fff;
    text-decoration: none;
    font-weight: bold;
    transition: .3s;
}
.bmenu a:hover{
    color: #007aff;
}